.cards {
  padding-top: 5rem;
  padding-left: 2rem;
  padding-right: 2rem;
  background: #fff;
}

.cards > h2,
.cards > p {
  padding-top: 10px;
  text-align: center;
}

.cards__container {
  display: flex;
  flex-flow: column;
  align-items: center;
  max-width: 1120px;
  width: 90%;
  margin: 0 auto;
}

.cards__wrapper {
  position: relative;
  margin: 10px 0 10px;
}

.cards__items {
  margin-bottom: 0px;
}

.cards__item {
  display: flex;
  flex: 1 1;
  margin: 0 1rem;
  border-radius: 10px;
  /* width: 300px; */
  width: 250px;
}

.cards__item__link {
  display: flex;
  flex-flow: column;
  width: 100%;
  box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);
  filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  border-radius: 10px;
  overflow: hidden;
  text-decoration: none;
}

.cards__item__pic-wrap {
  position: relative;
  width: 100%;
  padding-top: 67%;
  overflow: hidden;
}

.fade-img {
  animation-name: fade-img;
  animation-duration: 2s;
}

.cards__item__pic-wrap::after {
  content: attr(data-category);
  position: absolute;
  bottom: 0;
  margin-left: 10px;
  padding: 6px 8px;
  max-width: calc((100%) - 20px);
  font-size: 12px;
  font-weight: 800;
  color: rgb(255, 255, 255);
  background-color: #1f98f4; 
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

.cards__item__img {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: block;
  width: 100%;
  max-width: 100%;
  height: 100%;
  max-height: 100%;
  object-fit: cover;
  transition: all 0.2s linear;
}

.cards__item__img:hover {
  transform: scale(1.1);
}

.cards__item__info {
  padding: 20px 30px 30px;
}

.cards__item__text {
  color: #252e48;
  font-size: 18px;
  line-height: 24px;
}

.card__icon {
  font-size: 55px;
  text-align: center;
}

.card {
  border: none;
}

.card-header {
  background-color: transparent;
  border-bottom: transparent 1.5px;
}

.p-10 {
  padding: 0rem 10rem;
}

