}

.fa-play-circle {
  margin-left: 4px;
}

.btn-mobile {
  padding-left: calc(25vw - 80px);
}

.btn {
  width: 250px;
}

@media screen and (max-width: 960px) {
  .hero-container .text > h1 {
    font-size: 40px;
    margin-top: -20px;
  }
  .btn-mobile {
    padding-left: calc(50vw - 90px);
  }
}

@media screen and (max-width: 768px) {
  .hero-container .text > h1 {
    font-size: 25px;
    margin-top: 0px;
  }

  .hero-container .text > h2 {
    font-size: 20px;
  }

  .btn-mobile {
    display: block;
    text-decoration: none;
    padding-left: 0;
  }

  .btn {
    width: 100%;
  }
}

/* Arrow & Hover Animation */
.content-arrow {
  padding-top: 100px; }

#more-arrows {
  width: 75px;
  height: 65px; }
  #more-arrows:hover polygon {
    fill: #FFF;
    transition: all .2s ease-out; }
    #more-arrows:hover polygon.arrow-bottom {
      transform: translateY(-18px); }
    #more-arrows:hover polygon.arrow-top {
      transform: translateY(18px); }

polygon {
  fill: #FFF;
  transition: all .2s ease-out; }
  polygon.arrow-middle {
    opacity: 0.75; }
  polygon.arrow-top {
    opacity: 0.5; }

